import React, {Component} from 'react';
import {
  Table,
  TableBody,
  TableFooter,
  TableHeader,
  TableHeaderColumn,
  TableRow,
  TableRowColumn,
} from 'material-ui/Table';
import TextField from 'material-ui/TextField';
import Toggle from 'material-ui/Toggle';
import {Card, CardActions, CardHeader, CardText} from 'material-ui/Card';
import Chip from 'material-ui/Chip';

import IconMenu from 'material-ui/IconMenu';
import MenuItem from 'material-ui/MenuItem';
import IconButton from 'material-ui/IconButton';
import FilterListIcon from 'material-ui/svg-icons/content/filter-list';
import CloseIcon from 'material-ui/svg-icons/navigation/close';
import FavoriteIcon from 'material-ui/svg-icons/action/favorite';
import RefreshIcon from 'material-ui/svg-icons/navigation/refresh';
import CancelIcon from 'material-ui/svg-icons/navigation/cancel';


const styles = {
  propContainer: {
    width: 200,
    overflow: 'hidden',
    margin: '20px auto 0',
  },
  propToggleHeader: {
    margin: '20px auto 10px',
  },
};


const tableData = [
  {
    name: '請負　花子',
    sex: '女',
    status: '千葉県千葉市中央区千葉１－１－１０',
  },
  {
    name: '派遣　太郎',
    sex: '男',
    status: '千葉県千葉市中央区西千葉２－５',
  },
  {
    name: '請負　花見',
    sex: '女',
    status: '千葉県千葉市中央区千葉１－１－１０',
  },
  {
    name: '請負　ハナ',
    sex: '女',
    status: '千葉県千葉市中央区西千葉２－５',
  },
  {
    name: '請負　花子',
    sex: '女',
    status: '千葉県千葉市中央区千葉１－１－１０',
  },
  {
    name: '派遣　太郎',
    sex: '男',
    status: '千葉県千葉市中央区西千葉２－５',
  },
  {
    name: '請負　花子',
    sex: '女',
    status: '千葉県千葉市中央区千葉１－１－１０',
  },
];

/**
 * A more complex example, allowing the table height to be set, and key boolean properties to be toggled.
 */
export default class StaffList extends Component {

	/*
	 * コンストラクタ
	 */
  constructor(props) {
    super(props);
		/*
		 * チップ変数
		 */
    this.chipState = {chipData: [
      {key: 0, label: 'スタッフ番号'},
      {key: 1, label: '氏名'},
      {key: 2, label: '住所'},
      {key: 3, label: '削除不可'},
    ]};
    this.chipStyles = {
      chip: {
        margin: 4,
      },
      wrapper: {
        display: 'flex',
        flexWrap: 'wrap',
      },
    };
		/*
		 * アイコンメニュー変数
		 */
	  this.iconMenuState = {
	    valueSingle: '3',
	  };
		/*
		 * テーブル変数
		 */
	  this.state = {
	    fixedHeader: false,
	    fixedFooter: false,
	    stripedRows: false,
	    showRowHover: false,
	    selectable: false,
	    multiSelectable: false,
	    enableSelectAll: false,
	    deselectOnClickaway: false,
	    showCheckboxes: false,
	    height: '',
	  };
  }

	/*
	 * チップ関数
	 */
  handleRequestDelete = (key) => {
    if (key === 3) {
      alert('削除不可');
      return;
    }

    this.chipData = this.chipState.chipData;
    const chipToDelete = this.chipData.map((chip) => chip.key).indexOf(key);
    this.chipData.splice(chipToDelete, 1);
    this.setState({chipData: this.chipData});
  };
  renderChip(data) {
    return (
      <Chip
        key={data.key}
        onRequestDelete={() => this.handleRequestDelete(data.key)}
        style={this.chipStyles.chip}
      >
        {data.label}
      </Chip>
    );
  }

	/*
	 * アイコンメニュー関数
	 */
  handleChangeSingle = (event, value) => {
    this.setState({
      valueSingle: value,
    });
  };
  renderIcon(data) {
    return (
      <IconMenu
        iconButtonElement={<IconButton tooltip="お気に入りリスト" touch={true}><FavoriteIcon /></IconButton>}
        onChange={this.handleChangeSingle}
        value={this.iconMenuState.valueSingle}
      >
        <MenuItem value="1" primaryText="Refresh" />
        <MenuItem value="2" primaryText="Send feedback" />
        <MenuItem value="3" primaryText="Settings" />
        <MenuItem value="4" primaryText="Help" />
        <MenuItem value="5" primaryText="Sign out" />
      </IconMenu>
    );
  }

	/*
	 * カード展開コールバック
	 */
  expandChange = (newExpandedState: boolean) => {
      alert(newExpandedState);
      this.child={color: 'red'};
      return;
  };

	/*
	 * テーブル関数
	 */
  handleToggle = (event, toggled) => {
    this.setState({
      [event.target.name]: toggled,
    });
  };
  handleChange = (event) => {
    this.setState({height: event.target.value});
  };

  render() {
    return (
      <div>
  		<Card
  		  onExpandChange={this.expandChange}
  		>
		    <CardHeader
		      title="スタッフ一覧"
		      actAsExpander={false}
		      showExpandableButton={true}
		      openIcon={<CloseIcon />}
		      closeIcon={<FilterListIcon />}
		      iconStyle={{color: 'blue'}}
		    >
	      	<IconButton tooltip="再読み込み" touch={true}><RefreshIcon /></IconButton>
	      	<IconButton tooltip="検索条件クリア" touch={true}><CancelIcon /></IconButton>
	      	{this.renderIcon()}
		    </CardHeader>

		    <CardText expandable={true}>
		    検索条件
		    </CardText>

		    <CardActions
		      style={this.chipStyles.wrapper}
		    >
          {this.chipState.chipData.map(this.renderChip, this)}
		    </CardActions>

        <Table
          height={this.state.height}
          fixedHeader={this.state.fixedHeader}
          fixedFooter={this.state.fixedFooter}
          selectable={this.state.selectable}
          multiSelectable={this.state.multiSelectable}
        >
          <TableHeader
            displaySelectAll={this.state.showCheckboxes}
            adjustForCheckbox={this.state.showCheckboxes}
            enableSelectAll={this.state.enableSelectAll}
          >

            <TableRow>
              <TableHeaderColumn tooltip="スタッフ番号">スタッフ番号</TableHeaderColumn>
              <TableHeaderColumn tooltip="氏名">氏名</TableHeaderColumn>
              <TableHeaderColumn tooltip="性別">性別</TableHeaderColumn>
              <TableHeaderColumn tooltip="住所">住所</TableHeaderColumn>
            </TableRow>
          </TableHeader>
          <TableBody
            displayRowCheckbox={this.state.showCheckboxes}
            deselectOnClickaway={this.state.deselectOnClickaway}
            showRowHover={this.state.showRowHover}
            stripedRows={this.state.stripedRows}
          >
            {tableData.map( (row, index) => (
              <TableRow key={index}>
                <TableRowColumn>{100000000000 + index}</TableRowColumn>
                <TableRowColumn>{row.name}</TableRowColumn>
                <TableRowColumn>{row.sex}</TableRowColumn>
                <TableRowColumn>{row.status}</TableRowColumn>
              </TableRow>
              ))}
          </TableBody>
          <TableFooter
            adjustForCheckbox={this.state.showCheckboxes}
          >
            <TableRow>
              <TableRowColumn>ID</TableRowColumn>
              <TableRowColumn>Name</TableRowColumn>
              <TableRowColumn>Sex</TableRowColumn>
              <TableRowColumn>Status</TableRowColumn>
            </TableRow>
            <TableRow>
              <TableRowColumn colSpan="4" style={{textAlign: 'right'}}>
                Super Footer
              </TableRowColumn>
            </TableRow>
          </TableFooter>
        </Table>

        <div style={styles.propContainer}>
          <h3>Table Properties</h3>
          <TextField
            floatingLabelText="Table Body Height"
            defaultValue={this.state.height}
            onChange={this.handleChange}
          />
          <Toggle
            name="fixedHeader"
            label="Fixed Header"
            onToggle={this.handleToggle}
            defaultToggled={this.state.fixedHeader}
          />
          <Toggle
            name="fixedFooter"
            label="Fixed Footer"
            onToggle={this.handleToggle}
            defaultToggled={this.state.fixedFooter}
          />
          <Toggle
            name="selectable"
            label="Selectable"
            onToggle={this.handleToggle}
            defaultToggled={this.state.selectable}
          />
          <Toggle
            name="multiSelectable"
            label="Multi-Selectable"
            onToggle={this.handleToggle}
            defaultToggled={this.state.multiSelectable}
          />
          <Toggle
            name="enableSelectAll"
            label="Enable Select All"
            onToggle={this.handleToggle}
            defaultToggled={this.state.enableSelectAll}
          />
          <h3 style={styles.propToggleHeader}>TableBody Properties</h3>
          <Toggle
            name="deselectOnClickaway"
            label="Deselect On Clickaway"
            onToggle={this.handleToggle}
            defaultToggled={this.state.deselectOnClickaway}
          />
          <Toggle
            name="stripedRows"
            label="Stripe Rows"
            onToggle={this.handleToggle}
            defaultToggled={this.state.stripedRows}
          />
          <Toggle
            name="showRowHover"
            label="Show Row Hover"
            onToggle={this.handleToggle}
            defaultToggled={this.state.showRowHover}
          />
          <h3 style={styles.propToggleHeader}>Multiple Properties</h3>
          <Toggle
            name="showCheckboxes"
            label="Show Checkboxes"
            onToggle={this.handleToggle}
            defaultToggled={this.state.showCheckboxes}
          />
        </div>
  		</Card>
      </div>
    );
  }
}



